  position: absolute;
  display: flex;
  align-items: center;
  height: 18px;
  padding-left: 24px;
  color: #ffffff;
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 10px;
  cursor: pointer; }
  .checkbox span::before {
    content: "";
    position: absolute;
    left: 0;
    width: 18px;
    height: 18px;
    background-color: #ffffff; }

